
// import VuePhoneNumberInput from 'vue-phone-number-input'
import 'vue-phone-number-input/dist/vue-phone-number-input.css'
import { mapMutations, mapState } from 'vuex'

export default {
  name: 'UserAuth',
  components: {
    VuePhoneNumberInput: () => import('vue-phone-number-input')
  },
  layout: 'auth',
  data () {
    return {
      focus: null,
      country: null,
      enteredPhone: null,
      recaptcha: null,
      resend_counter: null,
      result: null,
      enterOTP: false,
      verifier: null,
      otp: {},
      otpDigits: ['a', 'b', 'c', 'd', 'e', 'f'],
      username: null
    }
  },
  head () {
    return {
      title: 'ðŸ“ž Jambo Talk - Login'
    }
  },
  computed: mapState(['darkTheme']),
  watch: {
    otp: {
      handler ({ a, b, c, d, e, f }) {
        const arr = [a, b, c, d, e, f]
        if (arr.findIndex(v => v && `${v}`.length !== 1) === -1) {
          return
        }
        for (let i = 0; i < arr.length; i++) {
          const val = `${arr[i]}`
          if (val.length > 1 && val.length !== 6) {
            this.defaultOtp()
            return setTimeout(() => {
              this.$refs.OTPinputs[0].focus()
            }, 100)
          }
          if (val.length === 6) {
            this.$refs.OTPinputs.forEach((el) => {
              el.blur()
            })
            const [a, b, c, d, e, f] = val.split('')
            return setTimeout(() => {
              this.otp = {
                a, b, c, d, e, f
              }
            }, 50)
          }
        }
      },
      deep: true
    }
  },
  mounted () {
    const otp = document.querySelectorAll('.otp')
    if (otp[0]) {
      otp.forEach((el) => {
        el.addEventListener('focus', (e) => { el.value = '' })
        el.addEventListener('input', (e) => { el.nextElementSibling ? el.nextElementSibling.focus() : el.blur() })
      })
    }
    this.defaultOtp()
  },
  methods: {
    ...mapMutations(['saveCreds']),
    async setupVerify () {
      if (this.verifier) {
        return Promise.resolve(this.verifier)
      }
      const { RecaptchaVerifier } = this.$fireModule.auth
      this.verifier = new RecaptchaVerifier(this.$refs.recaptcha, { size: 'invisible' })
      await this.verifier.verify()
      this.recaptcha = this.verifier
      return Promise.resolve(this.recaptcha)
    },
    register () {
      const { isValid, formattedNumber } = this.enteredPhone || {}
      if (!isValid) {
        return this.$nuxt.$emit('alert', { msg: 'Invalid phone', role: 'warn', timeout: 3000 })
      } else {
        this.$nuxt.$emit('alert', { msg: 'Validating...', role: 'loading', loading: true })
      }
      return this.setupVerify()
        .then((recaptcha) => {
          return this.$fire.auth.signInWithPhoneNumber(formattedNumber, recaptcha)
        })
        .then((result) => {
          this.result = result
          this.defaultOtp()
          this.$nuxt.$emit('alert', {})
          this.enterOTP = true
          this.username = formattedNumber
        })
        .catch((err) => {
          const msg = (err && err.code) ? err.message : 'Error, please reload page'
          console.log({ msg }, err)
          return this.$nuxt.$emit('alert', { msg: 'Error, please reload page', role: 'error', timeout: 3000 })
        })
    },
    resendCounter (duration = 115) {
      let timer = duration
      let minutes
      let seconds
      const tm = setInterval(() => {
        minutes = parseInt(timer / 60, 10)
        seconds = parseInt(timer % 60, 10)

        minutes = minutes < 10 ? '0' + minutes : minutes
        seconds = seconds < 10 ? '0' + seconds : seconds
        this.resend_counter = `in ${minutes}:${seconds}`
        if (--timer < 0) {
          this.resend_counter = null
          clearInterval(tm)
        }
      }, 1000)
    },
    defaultOtp () {
      const otp = {}
      this.otpDigits.forEach((o) => {
        otp[o] = 'â—'
      })
      this.otp = otp
    },
    resendOtp () {
      if (this.resend_counter) {
        return
      }
      this.resendCounter()
      this.$fire.auth.signInWithPhoneNumber(this.username, this.recaptcha)
        .then((res) => {
          this.result = res
          this.defaultOtp()
          this.enterOTP = true
        })
        .catch((err) => {
          console.error(err)
        })
    },
    verifyOTP () {
      const code = Object.values(this.otp).join('')
      if (!this.result) {
        return this.$nuxt.$emit('alert', { msg: 'Error, reload page.', role: 'error' })
      }
      this.enterOTP = false
      this.$nuxt.$emit('alert', { msg: 'Verifying...', role: 'loading', loading: true })
      this.result
        .confirm(code)
        .then(async () => {
          const token = await this.$fire.auth.currentUser.getIdToken()
          const { phoneNumber: phone, uid } = this.$fire.auth.currentUser
          this.$axios.setToken(token, 'Bearer')
          try {
            const creds = await this.$axios.$get(`v0.1/register/${uid}`)
            this.saveCreds({ ...creds, username: phone })
            this.$nuxt.$emit('alert', { msg: 'Registration successful', role: 'success', timeout: 3300 })
          } catch (error) {
            return this.$nuxt.$emit('alert', { msg: 'Error. Refresh page', role: 'error' })
          }
          return setTimeout(() => {
            return this.$router.push('/')
          }, 1000)
        })
        .catch(() => {
          this.enterOTP = true
        })
    }
  }
}
